#Home{
    position: relative;
    background: url('../../assets/bgIMG.png');
    background-size: cover;
    background-repeat: repeat;
    background-position: center;
    .app__wrapper{
        padding: 0;
        .copyright{
            display: none;
        }

    }

}
.app__header{
    flex: 1;
    width: 100%;
    height: 100%;
    flex-direction: row;
    padding: 6rem 2rem 0;
    @media screen and (min-width: 2000px) {
        padding-top: 8rem;
    }
    @media screen and (max-width: 1200px) {
       flex-direction: column;
    }
    @media screen and (max-width: 450px) {
        padding-top: 6rem 1rem 2rem;
    }
  
}
.app__header-info{
    flex: 0.65;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: flex-start;
    height: 100%;
    @media screen and (max-width:1200px) {
        width: 100%;
        margin-right: 0rem;
        
    }

}
.app__header-badge{
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    .badge-cmp, .tag-cmp{
        padding:1rem 2rem;
        border:var(--white-color);
        border-radius: 15px;
        flex-direction: row;
        width: auto;
        box-shadow: 0 0 20px rgba(0,0,0,0.3);
    }
    .tag-cmp{
        flex-direction: column;
        margin-top: 3rem;
        p{
            text-transform: uppercase;
            text-align: right;
        }
        
    }
    span{
        font-size: 2.5rem;
        @media screen and (min-width:2000px) {
            font-size: 5rem;
            
        }
    }

    @media screen and (max-width:1200px) {
           justify-content: flex-start;
           align-items: flex-start; 
        
    }

}
.app__header-circles {
    flex: 0.75;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    height: 100%;
    margin-left: 1rem;
  
    div {
      width: 100px;
      height: 100px;
      border-radius: 50%;
      align-items: center;
      display: flex;
      align-items:start;
      justify-content: start;
  
      background: var(--white-color);
      box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
  
      img {
        width: 60%;
        height: 60%;
        margin-top: 1rem;
        margin-left: 1rem;

      }

    }
    div:nth-child(1){
        width: 80px;
        height: 80px;
    }
    div:nth-child(2){
        margin: 1.75rem;

        width: 100px;
        height: 100px;
    }
    div:nth-child(3){
        width: 70px;
        height: 70px;
    }
    @media screen and (min-width:2000px) {
        div:nth-child(1){
            width: 400px;
            height: 400px;
        }
        div:nth-child(2){
    
            width: 170px;
            height: 170px;
        }
        div:nth-child(3){
            width: 200px;
            height: 200px;
        }
    }
    @media screen and (max-width:1200px) {
        width: 100%;
        flex-direction:row ;
        flex-wrap: wrap;
        margin-left: 0;

        div{
            margin:1rem;
        }

        
    }
}
.app__header-img{
    flex: 1;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position:relative;
    img{
        width: 70%;
        object-fit: contain;
        z-index: 1;

    }
    .overlay__circle{
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 0;
        width: 100%;
        height: 100%;
    }
    @media screen and (max-width:1200px){
        margin: 2rem 0;

        
    }
}